*,
*:after,
*:before {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  outline: none;
  -webkit-overflow-scrolling: touch;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  padding: 0;
  border: 0;
  margin: 0;
  vertical-align: baseline;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

ul, ol {
  margin: 0;
  padding: 0;
}

input,
button {
  outline: none;
  border-radius: 0;
  border: 0;
  margin: 0;
  background: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.clear {
  clear: both;
}

.clear:after {
  content: "";
  display: block;
  clear: both;
}

body {
  background-color: #E5E5E5;
  padding-top: 60px;
  padding-bottom: 60px;
  font-family: "Roboto", sans-serif;
}

.section + .section {
  margin-top: 100px;
}
.section__header {
  margin-bottom: 45px;
}
.section__title {
  font-size: 2rem;
  margin-bottom: 0;
}

.container {
  width: 100%;
  max-width: 1320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.rectangle {
  width: 500px;
  height: 300px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #FFF;
}

.circle {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: center;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #FF0000;
}

.icecream {
  width: 0px;
  height: 0px;
  border-width: 26px 20px 0 20px;
  border-style: solid;
  position: relative;
  border-color: #fff transparent transparent transparent;
}
.icecream:after {
  content: "";
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #FFF;
  position: absolute;
  bottom: calc(100% + 26px);
  left: 50%;
  transform: translateX(-50%);
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, minmax(320px, 1fr));
  background-color: #FFF;
}
@media screen and (max-width: 1320px) {
  .grid {
    grid-template-columns: repeat(3, minmax(320px, 1fr));
  }
}
@media screen and (max-width: 1024px) {
  .grid {
    grid-template-columns: repeat(2, minmax(320px, 1fr));
  }
}
@media screen and (max-width: 768px) {
  .grid {
    grid-template-columns: 1fr;
  }
}
.grid__col {
  border-right: 1px solid #999;
  border-bottom: 1px solid #999;
  padding: 20px;
}
@media screen and (min-width: 1320px) {
  .grid__col:nth-child(4n+4) {
    border-right: none;
  }
  .grid__col:nth-last-child(-n+4) {
    border-bottom: none;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1320px) {
  .grid__col:nth-child(3n+3) {
    border-right: none;
  }
  .grid__col:nth-last-child(-n+3) {
    border-bottom: none;
  }
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .grid__col:nth-child(2n+2) {
    border-right: none;
  }
  .grid__col:nth-last-child(-n+2) {
    border-bottom: none;
  }
}
@media screen and (max-width: 768px) {
  .grid__col {
    border-left: none;
    border-right: none;
  }
}

.grid--flex {
  display: flex;
  flex-wrap: wrap;
}
.grid--flex > * {
  width: 25%;
}
@media screen and (max-width: 1320px) {
  .grid--flex > * {
    width: 33.333%;
  }
}
@media screen and (max-width: 1024px) {
  .grid--flex > * {
    width: 50%;
  }
}
@media screen and (max-width: 768px) {
  .grid--flex > * {
    width: 100%;
  }
}

.card {
  background-color: rebeccapurple;
  height: 200px;
}
.card--pink {
  background-color: #F2A5A3;
}
.card--cyan {
  background-color: #89E0CD;
}
.card--dark-pink {
  background-color: #C058CB;
}
.card--blue {
  background-color: #5844E2;
}
.card--purple {
  background-color: #70356F;
}
.card--brown {
  background-color: #622926;
}
.card--purple-dark {
  background-color: #282974;
}
.card--purple-darker {
  background-color: #141B49;
}
.card--green {
  background-color: #1A3413;
}
.card--brown-light {
  background-color: #2C1E1D;
}
.card--green-dark {
  background-color: #0C170C;
}
.card--black {
  background-color: #000000;
}

.list > li {
  list-style: decimal inside none;
  padding: 20px 10px;
  font-size: 20px;
  font-weight: 500;
  color: #323276;
}
.list > li::marker {
  color: #333;
}
.list > li:nth-child(odd) {
  background-color: #EB4B23;
  color: #FFF;
}
.list > li:nth-child(4n+4) {
  background-color: #407D22;
  color: #FFF;
}

.field {
  min-height: 300px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  border-bottom: 1px solid #999;
}

.ball {
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #EB4B23;
  animation: bounce infinite 1.5s ease-in-out;
  animation-delay: 2s;
}

@keyframes bounce {
  20% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  70% {
    transform: translateY(0);
  }
  90% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(0);
  }
  0% {
    transform: translateY(-150px);
  }
  35% {
    transform: translateY(-70px);
  }
  60% {
    transform: translateY(-30px);
  }
  80% {
    transform: translateY(-15px);
  }
  97% {
    transform: translateY(-7px);
  }
}
.box-container {
  position: relative;
  width: 800px;
  height: 600px;
  border: 1px solid red;
  margin: 0 auto;
}

.box {
  width: 50px;
  height: 50px;
  background-color: #5DB557;
  position: absolute;
  left: 50%;
  top: 100%;
  transform: translate(-50%, -100%);
}

.box-action {
  margin-top: 30px;
  text-align: center;
}

.start-animation {
  background-color: royalblue;
  padding: 8px 16px;
  color: #FFF;
}
.start-animation:hover, .start-animation:focus, .start-animation:active {
  background-color: #2a4fbd;
}